<template lang="html">
  <div class="follow-up">
    <div class="btn">
      <el-button type="primary" @click="followUpDialogFormVisible = true"></el-button>
      <el-button type="primary" @click="qryAllSchoolInfo"></el-button>
      <el-button type="primary" @click="getStudentInfo"></el-button>
    </div>

    <el-tabs class="fllowup-tab" v-model="activeName2" type="card" @tab-click="handleClick">
      <el-tab-pane label="" name="followUp">
        <el-row :gutter="20" style="padding:0px 15px;">
          <el-col
            :span="12"
            v-for="(item,index) in studentFollowUp"
            :key="index">
            <el-card class="box-card">
              <div slot="header" class="clearfix" v-if="item.followMode || item.followMode==0">
                <span class="title">{{wordbook.followMode[item.followMode].label || ''}}<strong v-show="item.source == 1">()</strong></span>
                <p class="right" type="text">{{item.followTime}}</p>
              </div>
              <ul class="list text item">
                <li v-show="item.isConnect == 0 || item.isConnect == 1">
                  <strong></strong>
                  <span v-if="item.isConnect || item.isConnect == 0">{{wordbook.isConnect[item.isConnect].label}}</span>
                </li>
                <li >
                  <strong></strong>
                  <span  v-if="item.isValid || item.isValid == 0">{{ item.isValid == 0 ? '' : '' }}</span>
                </li>
                <li>
                  <strong></strong>
                  <span>{{item.backTime}}</span>
                </li>
                <li>
                  <strong></strong>
                  <span class="remarks">{{item.remark}}</span>
                </li>
              </ul>
            </el-card>
          </el-col>
          <!--  -->
          <el-col
            :span="12"
            v-for="(item,index) in myAddFriendList"
            :key="index+200">
            <el-card class="box-card">
              <div slot="header" class="clearfix">
                <span class="title"></span>
                <p class="right" type="text">{{item.createTime}}</p>
              </div>
              <ul class="list text item">
                <li>
                  <strong></strong>
                  <span>{{item.operateName}}</span>
                </li>
                <li>
                  <strong></strong>
                  <span v-if="item.type || item.type==0">{{wordbook.followMode[item.type].label}}</span>
                </li>
                <li>
                  <strong></strong>
                  <span>{{item.remark}}</span>
                </li>
              </ul>
            </el-card>
          </el-col>

          <!--  -->
          <el-col
            :span="12"
            v-for="(item,index) in editStudentList"
            :key="index+300">
            <el-card class="box-card">
              <div slot="header" class="clearfix">
                <span class="title"></span>
                <p class="right" type="text">{{item.createTime}}</p>
              </div>
              <ul class="list text item">
                <li>
                  <strong></strong>
                  <span>{{item.operateName}}</span>
                </li>
                <li v-if="item.newMobile || item.mobile">
                  <strong ></strong>
                  <span>{{item.mobile}} <i class="el-icon-d-arrow-right"></i> {{item.newMobile}} </span>
                </li>
                <li v-if="item.newWechat || item.wechat">
                  <strong></strong>
                  <span>{{item.wechat}} <i class="el-icon-d-arrow-right"></i> {{item.newWechat}} </span>
                </li>
                <li v-if="item.newQq || item.qq">
                  <strong>QQ</strong>
                  <span>{{item.qq}} <i class="el-icon-d-arrow-right"></i> {{item.newQq}} </span>
                </li>
                <li v-if="item.newPhone || item.phone">
                  <strong></strong>
                  <span>{{item.phone}} <i class="el-icon-d-arrow-right"></i> {{item.newPhone}} </span>
                </li>
                <li v-if="item.proName || item.newProName">
                  <strong></strong>
                  <span>{{item.proName}} <i class="el-icon-d-arrow-right"></i> {{item.newProName}} </span>
                </li>
                <li v-if="item.newRegionName || item.regionName">
                  <strong></strong>
                  <span>{{item.regionName}} <i class="el-icon-d-arrow-right"></i> {{item.newRegionName}} </span>
                </li>
              </ul>
            </el-card>
          </el-col>
        </el-row>
      </el-tab-pane>
      <el-tab-pane label="" name="schoolOrder">
        <el-row :gutter="20" style="padding:0px 15px;">
          <el-col
            :span="12"
            v-for="(item,index) in schoolReservation"
            :key="index">
            <el-card class="box-card">
              <div slot="header" class="clearfix">
                <span class="title">{{item.schoolName}}</span>
                <p class="right" type="text">{{item.createTime}}</p>
              </div>
              <ul class="list text item">
                <li>
                  <strong></strong>
                  <span>{{item.proName}}</span>
                </li>
                <li >
                  <strong></strong>
                  <span>{{item.cname}}</span>
                </li>
                <li>
                  <strong></strong>
                  <span>{{item.price}}</span>
                </li>
                <li>
                  <strong></strong>
                  <span>{{item.remark}}</span>
                </li>
              </ul>
            </el-card>
          </el-col>

        </el-row>
      </el-tab-pane>
      <el-tab-pane label="" name="dealOrder">
        <el-row :gutter="20" style="padding:0px 15px;">
          <el-col
            :span="12"
            v-for="(item,index) in orderList"
            :key="index">
            <el-card class="box-card">
              <div slot="header" class="clearfix">
                <span class="title">ID:{{item.sn}}</span>
                <p class="right" type="text">{{item.createTime}}</p>
              </div>
              <ul class="list text item dealOrder">
                <li>
                  <strong></strong>
                  <span>{{item.name}}</span>
                </li>
                <li >
                  <strong></strong>
                  <span>{{item.cardNo}}</span>
                </li>
                <li>
                  <strong></strong>
                  <span>{{item.regionName}}</span>
                </li>
                <li>
                  <strong></strong>
                  <span>{{item.proName}}</span>
                </li>
                <li>
                  <strong></strong>
                  <span>{{item.cname}}</span>
                </li>
                <li>
                  <strong></strong>
                  <span>{{item.price}}</span>
                </li>
                <li>
                  <strong></strong>
                  <span>{{item.offsetAmount}}</span>
                </li>
                <li>
                  <strong></strong>
                  <span>{{item.amount}}</span>
                </li>
              </ul>
            </el-card>
          </el-col>
        </el-row>
      </el-tab-pane>
      <el-tab-pane label="" name="callRecord">
        <el-row :gutter="20" class="padding-left-right-20">
          <el-col :span="12" v-for="(item,index) in contactMsg" :key="index" class="margin-bottom-10">
            <el-card>
              <div slot="header" class="clear">
                <span>{{item.userName}}</span>
                <span class="right">{{item.followTime}}</span>
              </div>
              <div>
                <div class="lineHeight-40">{{item.isValid?'':''}}</div>
                <div class="lineHeight-40">{{item.duration}}</div>
                <div class="lineHeight-40">
                  <el-button type="text" class="margin-right-10" @click="playAudio" v-if="item.url">
                    
                  </el-button>
                  <audio :src="item.url" controls="controls" preload hidden/>
                  <a :href="item.url" download="tanzhouCRM" class="downloadBtn" v-if="item.url"></a>
                </div>
                <div><span class="lineHeight-40"></span>{{item.remark}}</div>
              </div>
            </el-card>
          </el-col>
        </el-row>
      </el-tab-pane>
      <el-tab-pane label="" name="chatOnline"></el-tab-pane>
      <el-tab-pane label="" name="roamLog">
        <el-row :gutter="20" style="padding:0px 15px;">
          <el-col
            :span="12"
            v-for="(item,index) in studentAllocateList"
            :key="index">
            <el-card class="box-card">
              <div slot="header" class="clearfix">
                <span class="title">{{wordbook.mode[item.mode]}}</span>
                <p class="right" type="text">{{item.createTime}}</p>
              </div>
              <ul class="list text item">
                <li>
                  <strong></strong>
                  <span>{{item.operateName}}</span>
                </li>
                <li >
                  <strong></strong>
                  <span>{{item.beforeUserName}}</span>
                </li>
                <li>
                  <strong></strong>
                  <span>{{item.userName}}</span>
                </li>
              </ul>
            </el-card>
          </el-col>

        </el-row>
      </el-tab-pane>
    </el-tabs>

    <!--   -->
    <el-dialog class="stick" title="" :visible.sync="followUpDialogFormVisible"  width="60%">
      <el-form :model="followUp" :rules="rules" ref="followUp" :inline="true" label-width="100px" class="demo-ruleForm">
        <el-form-item label="" prop="followTimeMinute">
          <el-date-picker
            v-model="followUp.followTimeMinute"
            type="datetime"
            placeholder=""
            value-format="yyyy-MM-dd HH:mm"
            format="yyyy-MM-dd HH:mm"
            default-time="12:00:00">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="followMode">
          <el-select v-model="followUp.followMode" placeholder="">
            <el-option
            v-for='item in wordbook.followMode'
            :key="item.id"
            :label="item.label"
            :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="isConnect" v-if="followUp.followMode == 1 || followUp.followMode == 0">
          <el-select v-model="followUp.isConnect" placeholder="" @change="playCallResult($event)">
            <el-option
              v-for='item in wordbook.isConnect'
              :key="item.id"
              :value="item.id" :label="item.label"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="isValid">
          <el-select v-model="followUp.isValid" placeholder="">
            <el-option
              v-for='item in wordbook.isValid'
              :disabled="followUp.followMode == 1 || followUp.followMode == 0"
              :key="item.id"
              :value="item.id"
              :label="item.label"
            ></el-option>
          </el-select>
        </el-form-item>
        <br>
        <el-form-item label="" >
          <el-input  type="textarea" v-model="followUp.remark" style="width:270%"></el-input>
        </el-form-item>
        <!-- <current title=""></current> -->
        <div class="g-title">
          <h2></h2>
        </div>

        <el-form-item label="" style="width:100%;">
          <el-select v-model="followUp.backPlan" placeholder="">
            <el-option
            v-for='item in wordbook.backPlan'
            :key="item.id"
            :value="item.id"
            :label="item.label">
            </el-option>
          </el-select>
          <el-date-picker
            v-model="followUp.backTimeMinute"
            v-if="followUp.backPlan !== 4"
            type="datetime"
            placeholder=""
            value-format="yyyy-MM-dd HH:mm"
            format="yyyy-MM-dd HH:mm"
            default-time="12:00:00">
          </el-date-picker>
        </el-form-item>
        <br>
        <el-form-item label=""  >
          <el-input  type="textarea" v-model="followUp.backRemark" style="width:270%"></el-input>
        </el-form-item>

        <el-form-item style=" width:100%; text-align:center; "  >
          <el-button @click="followUpDialogFormVisible = false"></el-button>
          <el-button type="success" @click="followUprEnter('followUp')"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <!--  -->
    <el-dialog class="stick" title="" :visible.sync="reservationsDialogFormVisible"  width="60%">
      <el-form :model="reservations" :rules="reservationsRules" ref="reservations" :inline="true" label-width="100px" class="demo-ruleForm">
        <el-form-item label="" prop="advanceTimeMinute">
          <el-date-picker
            v-model="reservations.advanceTimeMinute"
            type="datetime"
            placeholder=""
            value-format="yyyy-MM-dd HH:mm"
            format="yyyy-MM-dd HH:mm"
            default-time="12:00:00">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="schoolIdFk">
          <el-select v-model="reservations.schoolIdFk" placeholder="">
            <el-option
            v-for='item in allSchoolInfo'
            :key="item.id"
            :value="item.id"
            :label="item.name"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="proIdFk">
          <el-select v-model="reservations.proIdFk" placeholder="" @change="getProject($event)">
            <el-option
            v-for='item in intentSubject'
            :key="item.proId"
            :value="item.proId"
            :label="item.proName"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="ctIdFk">
          <el-select v-model="reservations.ctIdFk" placeholder="">
            <el-option
            v-for='item in projectClassType'
            :key="item.ctId"
            :value="item.ctId"
            :label="item.cname">
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="">
          <el-input v-model="reservations.price"></el-input>
        </el-form-item>
        <br>
        <el-form-item label=""  >
          <el-input  type="textarea" v-model="reservations.remark"></el-input>
        </el-form-item>
        <el-form-item style=" width:100%; text-align:center; "  >
          <el-button @click="reservationsDialogFormVisible = false"></el-button>
          <el-button type="success" @click="reservationsEnter('reservations')"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>


    <el-dialog class="stick" title="" :visible.sync="createOrderDialogFormVisible"  width="50%">
      <el-form :model="createOrder" :rules="createOrdeVerificationr" ref="createOrder" label-width="100px" class="demo-ruleForm">
        <el-form-item label="">
          <el-input v-model="createOrder.name"></el-input>
        </el-form-item>

        <el-form-item label="" style="width:100%;">
          <el-select  placeholder="" style="width:35%;" v-model="createOrder.cardType">
            <el-option v-for='(item,index) in idCardOptions' :label="item.label" :value="item.id" :key="index"></el-option>
          </el-select>
          <el-input  style="width:45%;" v-model="createOrder.cardNo"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-cascader
            :options="province"
            @change="handleItemChange($event)"
            v-model="sellCity"
            :change-on-select="true"
            clearable
            :props="provinceProps">
          </el-cascader>
        </el-form-item>
        <el-form-item label="" prop="proIdFk">
          <el-select v-model="createOrder.proIdFk" placeholder="" @change="getProject($event)">
            <el-option
            v-for='item in intentSubject'
            :key="item.proId"
            :value="item.proId"
            :label="item.proName"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="ctIdFk" style="width:100%;">
          <el-select v-model="createOrder.ctIdFk" placeholder="">
            <el-option
            v-for='item in projectClassType'
            :key="item.ctId"
            :value="item.ctId"
            :label="item.cname">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="price">
          <el-input type="" v-model.number="createOrder.price" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="offsetAmount">
          <el-input v-model.number="createOrder.offsetAmount"></el-input>
        </el-form-item>
        <el-form-item label="" style="width:100%;">
          <span>{{createOrder.price - favorablePrice}}</span>
        </el-form-item>
        <el-form-item style=" width:100%; text-align:center; "  >
          <el-button @click="createOrderDialogFormVisible = false"></el-button>
          <el-button type="success" @click="createOrderEvent('createOrder')"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

  </div>
</template>

<script>
import { isidcard, isGAT } from "@/common/util.js";
import { idCardOptions, editReturnList, isItConnected, studentContact, communicateResults } from "./../data/data.js"
export default {
  props: ['intentSubject'],
  components: {
  },
  computed:{
    //  
    favorablePrice(){
      const offsetAmount = this.createOrder.offsetAmount;
      return Math.round(this.createOrder.offsetAmount*100)/100
    }
  },
  data() {
      //   0
      var validateDiscountAmount = (rule, value, callback) => {

        if(this.createOrder.offsetAmount >= this.createOrder.price){
          callback(new Error(''));
        }
        else if (this.createOrder.offsetAmount <= 0) {
          callback(new Error('0'));
        }
        else {
          callback()
        }
      }
      return {
        activeName2: 'followUp',
        idCardOptions,
        contactMsg:[],
        studentFollowUp:[],
        // 
        followUpDialogFormVisible:false,
        reservationsDialogFormVisible:false,
        createOrderDialogFormVisible: false,
        // 
        projectClassType: [],
        // 
        allSchoolInfo: [],
        // 
        schoolReservation: [],
        // 
        myAddFriendList: [],
        // 
        editStudentList: [],
        // 
        orderList: [],
        // 
        studentAllocateList: [],
        province: [],
        provinceProps: {
          value: 'id',
          label: 'city',
          children: 'cities'
        },
        // 
        sellCity:[],
        // 
        wordbook:{
          followMode: studentContact,
          isConnect: isItConnected,
          isValid:communicateResults,
          backPlan: editReturnList,
          mode:['','','']
        },
        followUp:{
          followTimeMinute: "",
          followMode: "",
          isConnect: "",
          isValid: "",
          remark: "",
          backPlan: "",
          backTimeMinute: "",
          backRemark: ""
        },
        //  
        reservations:{
          advanceTimeMinute: "",
          schoolIdFk: "",
          proIdFk: "",
          ctIdFk: "",
          price: "",
          remark: ""
        },
        //  
        createOrder:{
          name: "",
          cardType: "",
          cardNo: "",
          regionIdFk: "",
          proIdFk: "",
          ctIdFk: "",
          offsetAmount: "",
          price: ""
        },
        rules:{
          followTimeMinute: [
              { required: true, message: '', trigger: 'change' }
            ],
          followMode: [
              { required: true, message: '', trigger: 'change' }
          ],
          isConnect: [
              { required: true, message: '', trigger: 'change' }
          ],
          isValid: [
              { required: true, message: '', trigger: 'change' }
          ]
        },
        // 
        reservationsRules:{
          advanceTimeMinute: [
              { required: true, message: '', trigger: 'change' }
            ],
          schoolIdFk: [
              { required: true, message: '', trigger: 'change' }
          ],
          proIdFk: [
              { required: true, message: '', trigger: 'change' }
          ],
          ctIdFk: [
              { required: true, message: '', trigger: 'change' }
          ]
        },
        // 
        createOrdeVerificationr:{
          proIdFk: [
              { required: true, message: '', trigger: 'change' }
          ],
          ctIdFk: [
              { required: true, message: '', trigger: 'change' }
          ],
          offsetAmount: [
            {validator: validateDiscountAmount, trigger: 'blur'},
            { type: 'number', message: ''}
          ],
          price: [
            { required: true, type: 'number', message: ''}
          ]
        },

      }
    },
    watch: {
      // 
     '$route': 'refreshRoute'
     },
    created (){
      this.refreshRoute();
      // 
      this.qryArea();
    },
    methods: {
      playAudio(evt) {
        //
        let targetElement = evt.target;
        if(targetElement.tagName!=='SPAN') return;
        let audio = targetElement.parentElement.parentElement.children[1];
        let handle=function () {
          //load
          audio.load();
          targetElement.innerText = '';
        };
        audio.removeEventListener("ended", handle, false);
        audio.addEventListener('ended', handle, false);
        if (audio.paused) {
          audio.play();// 
          targetElement.innerText = ''
        } else {
          audio.pause();// 
          targetElement.innerText = ''
        }
      },
      handleClick(tab, event) {
        if(this.activeName2 == "schoolOrder"){
          // 
          this.qryStudentAdvanceList();
        }
        else if(this.activeName2 == "dealOrder"){
          // 
          this.qryOrderList();
        }
        else if(this.activeName2 == "roamLog"){
          // 
          this.qryStudentAllocateList();
        }
      },
      // 
      refreshRoute(){
        this.userId = this.$store.state.user.userInfo.userId;
        //ID
        this.stuId = this.$router.currentRoute.query.stuId;
        // 
        this.getStuFollowDetailList();
        // -
        this.qryStudentFriendList();
        // - -
        this.qryStudentMdfLogList();
        this.getCalllLog();
      },
      getCalllLog(){
        const stuId=this.$router.currentRoute.query.stuId;
        this.API.getStuCallDetailList({stuId:stuId}).then((res)=>{
          this.contactMsg=res.list;
        });
      },
      // 
      getStuFollowDetailList(){
        this.API.getStuFollowDetailList({
          stuId: this.stuId
        })
        .then(res=>{
          this.studentFollowUp = res.list;
        }).catch(err => {
          console.log(err.message)
        });
      },
      // 
      followUprEnter(formName){
        this.$refs[formName].validate((valid) => {
          if(!valid) return;
          //  
          if(this.followUp.backPlan == 4){
            this.followUp.backTimeMinute = ""
          }

          this.followUp.stuIdFk = this.stuId;
          this.API.addStudentFollowDetail(this.followUp)
          .then(res =>{
            this.$message({
              message: '',
              type: 'success'
            });
            this.followUpDialogFormVisible = false;
            this.$router.go(0)
          }).catch(err => {
            console.log(err.message)
          });

        })
      },
      // -
      qryStudentFriendList(){
        this.API.qryStudentFriendList({
          stuId: this.stuId
        })
        .then(res =>{
          // console.log(res);
          this.myAddFriendList = res.list;
        }).catch(err => {
          console.log(err.message)
        });
      },
      //  - -
      qryStudentMdfLogList(){
        this.API.qryStudentMdfLogList({
          stuId: this.stuId
        })
        .then(res =>{
          console.log(res);
          this.editStudentList = res.list;
        }).catch(err => {
          console.log(err.message)
        });
      },
      // 
      reservationsEnter(formName){
        this.$refs[formName].validate((valid) => {
          if(!valid) return;

          this.reservations.stuIdFk = this.stuId;
          this.API.addStudentAdvance(this.reservations)
          .then(res =>{
            this.$message({
              message: '',
              type: 'success'
            })
            this.reservationsDialogFormVisible = false;
          })
          .catch(err => {
            console.log(err.message)
          });
        })
      },
      // 
      qryAllSchoolInfo(){
        this.reservationsDialogFormVisible = true
        this.API.qryAllSchoolInfo()
        .then(res =>{
          console.log(res.list);
          this.allSchoolInfo = res.list;
        }).catch(err => {
          console.log(err.message)
        });
      },
      //  
      qryStudentAdvanceList(){
        this.API.qryStudentAdvanceList({
          stuId: this.stuId
        })
        .then(res =>{
          this.schoolReservation = res.list
        }).catch(err => {
          console.log(err.message)
        });
      },
      //  
      getStudentInfo(){
        this.createOrderDialogFormVisible = true;
        this.API.getStuBaseInfo({
          stuId:this.stuId
        })
        .then(res=>{
          let stuInfo = res.result[0].stuInfo;
          this.createOrder.name =  stuInfo.name;
          this.createOrder.cardType =  stuInfo.cardType;
          this.createOrder.cardNo =  stuInfo.cardNo;
        }).catch(err => {
          console.log(err.message)
        });
      },
      //
      createOrderEvent(formName){
        if (this.createOrder.cardType == 1){
          if (!isidcard(this.createOrder.cardNo)) {
            this.$message({
              message: '',
              type: 'warning'
            })
            return;
          }
        }
        else if (this.createOrder.cardType == 2){
          if (!isGAT(this.createOrder.cardNo)) {
            this.$message({
              message: '',
              type: 'warning'
            })
            return;
          }
        }
        this.$refs[formName].validate((valid) => {
          if(!valid) return;
          this.createOrder.stuIdFk = this.stuId;
          this.API.addOrder(this.createOrder)
          .then(res =>{
            this.$message({
              message: '',
              type: 'success'
            })
            this.createOrderDialogFormVisible = false;
          })
          .catch(err => {
            console.log(err.message)
          });

        })
        //this.createOrder.dialogFormVisible = false;
      },
      // 
      qryOrderList(){
        this.API.qryOrderList({
          stuId: this.stuId
        })
        .then(res =>{
          //console.log(res);
          this.orderList = res.list;
        }).catch(err => {
          console.log(err.message)
        });
      },
      // 
      qryStudentAllocateList(){
        this.API.qryStudentAllocateList({
          stuId: this.stuId
        })
        .then(res =>{
          //console.log(res);
          this.studentAllocateList = res.list;
        }).catch(err => {
          console.log(err.message)
        });
      },
      // 
      getProject(id){
        this.API.qryAllClassType({
          proIdFk:id
        })
        .then(res =>{
          this.projectClassType = res.list;
        })
      },
      //  
      playCallResult(id){
        //console.log(id);
        if(id == 0){
          this.followUp.isValid = 1;
        }
        else{
          this.followUp.isValid = 0;
        }
      },
      // 
      qryArea(val){
        this.API.qryArea({areaId: 1}).then((res) => {
          let data = res.result.map((item) => {
            return {
              id: item.id,
              city: item.province || item.city,
              cities: []
            }
          });
          this.province = data;
        })
      },
      // 
      handleItemChange(val,key) {
        const province = this.province;
        this.API.qryArea({areaId: val[0] || val}).then((res) => {
          province.forEach((item) => {
            if (item.id == val[0] || item.id == val) {
              if(res.result[0].city){
                item.cities = res.result;
              }else {
                item.cities='';
              }
              return false;
            }

          })
        })
        //  ID
        if(this.sellCity[1]){
          this.createOrder.regionIdFk = this.sellCity[1]
        }
        else{
          this.createOrder.regionIdFk = this.sellCity[0]
        }


      },
    }
}
</script>

<style lang="scss" scoped>
  .follow-up{
    margin-left: 20px;
    background: #fff;
    padding-bottom: 30px;
    .btn{
      margin: 15px 0;
    }
    .fllowup-tab{
      background: #fff;
    }
    .box-card{
      margin-bottom: 20px;
      .el-card__header{
        padding: 10px 20px;
      }
      .el-card__body{
        padding: 0;
      }
      .list{
        padding: 0px 20px 10px 20px;
        overflow: hidden;
        border-bottom: 1px solid #f4f4f4;
        &.dealOrder{
          li{
            width: 50%;
          }
        }
        li{
          float: left;
          overflow: hidden;
          width: 100%;
          text-align: left;
          margin: 10px 0px;
          span{
            color: #2c2f33;
            display: block;
            //float: left;
          }
          strong{
            color: #848e99;
            float: left;
          }
          // 
          .remarks{
            line-height: 24px;
            margin-top: -1px;
            word-break: break-word;
          }
        }
      }
      .clearfix{

        .title{
          font-size: 14px;
          strong{
            color: #848e99;
            padding-left: 3px;
          }
        }
        .right{
          float: right; padding: 3px 0;
          color:#848e99;
          font-size: 13px;
        }
      }
    }
    .stick{
      .el-form-item{
        width: 45%;
        display: inline-block;
      }
    }
    //  flex flex float  
    .el-row{
      display: flex;
      flex-wrap: wrap;
    }
  }
</style>

<style lang="css">
.follow-up .el-card__header{
  padding: 10px 20px;
}
.follow-up .el-card__body{
  padding: 0;
}
</style>



