<template lang="html">
  <!--  -->
  <div class="studentInfo">
    <div class="item">
      <div class="studentHeader">
        <h4 class="slideTitle">
          <span></span>
          <h3></h3>
        </h4>
        <div class="progress">
          <span></span>
          <el-rate class="xinxin"
            v-model="studentIntention.value"
            disabled
            show-score
            text-color="#40b3ff"
            :score-template="studentIntention.label">
          </el-rate>
        </div>
        <div  class="btn-right">
          <el-button type="text" size="small" class="btn" @click="dialogFormVisible = true;"></el-button>
          <el-button type="text" size="small" class="btn" @click="addFriends"></el-button>
          <el-button type="text" size="small" class="btn" @click="edit"></el-button>
        </div>
      </div>
      <ul class="list">
        <li>
          <strong>ID</strong>
          <span>{{stuInfo.stuId}}</span>
        </li>
        <li>
          <strong></strong>
          <span>{{stuInfo.extendType || ''}}</span>
        </li>
        <li>
          <strong></strong>
          <span>{{intentSubjectSum()}}</span>
        </li>
        <li>
          <strong></strong>
          <span>{{stuInfo.intentProvinceName}} {{stuInfo.intentCityName}}</span>
        </li>
        <li>
          <strong></strong>
          <span>{{stuInfo.createTime}}</span>
        </li>
        <li>
          <strong></strong>
          <span>{{stuInfo.lastAllocTime}}</span>
        </li>
        <li>
          <strong></strong>
          <span>{{stuInfo.lastFollowTime || ''}}</span>
        </li>
        <li>
          <strong></strong>
          <span>{{stuInfo.lastValidFollowTime || ''}}</span>
        </li>
      </ul>
    </div>

    <div class="item"  >
      <div class="studentHeader">
        <h4 class="slideTitle">
          <span></span>
          <h3></h3>
        </h4>
      </div>

      <ul class="list">
        <li>
          <strong></strong>
          <span>{{stuInfo.nick || ''}}</span>
        </li>
        <li>
          <strong></strong>
          <span>{{stuInfo.name  || ''}}</span>
        </li>
        <li>
          <strong></strong>
          <span>{{stuInfo.provinceName}} {{stuInfo.cityName}}</span>
        </li>
        <li>

          <strong></strong>
          <span v-if="stuInfo.sex || stuInfo.sex == 0">{{sexList[stuInfo.sex-1].label}}</span>
          <span v-else></span>
        </li>
        <li>
          <strong></strong>
          <span>{{stuInfo.age  || ''}}</span>
        </li>
        <li>
          <strong>{{!stuInfo.cardType || stuInfo.cardType == 1 ? '' : ''}}</strong>
          <span>{{stuInfo.cardNo  || ''}}</span>
        </li>
      </ul>
    </div>

    <div class="item"  >
      <div class="studentHeader">
        <h4 class="slideTitle">
          <span></span>
          <h3></h3>
        </h4>
      </div>

      <ul class="list">
        <li>
          <strong></strong>
          <span v-if="stuInfo.identity || stuInfo.identity == 0">{{ idOptions[stuInfo.identity].label}}</span>
          <span v-else></span>
        </li>
        <li>
          <strong></strong>
          <span v-if="stuInfo.level || stuInfo.level == 0">{{ levelList[stuInfo.level].label}}</span>
          <span v-else></span>
        </li>
        <li>
          <strong></strong>
          <span v-if="stuInfo.classTime || stuInfo.classTime == 0">{{ classtimeList[stuInfo.classTime].label}}</span>
          <span v-else></span>
        </li>
        <li>
          <strong></strong>
          <span>{{stuInfo.startStudyTime || ''}}</span>
        </li>
        <li>
          <strong></strong>
          <span class="study-use">{{stuInfo.purpose || ''}}</span>
        </li>
      </ul>
    </div>
    <div class="item">
      <div class="studentHeader">
        <h4 class="slideTitle">
          <span></span>
          <h3></h3>
        </h4>
      </div>

      <div class="pay">
        <ul class="list">
          <li style="width:100%; text-align:left;">
            <strong style="width:150px;"></strong>
            <span >
              <span v-if="EditReturn.backPlan ||EditReturn.backPlan === 0">{{editReturnList[EditReturn.backPlan].label || ''}}</span>
              &nbsp;&nbsp;
              <i v-show="EditReturn.backPlan !== 4">{{EditReturn.backTime}}</i>
            </span>
          </li>

          <li style="width:100%; text-align:left;">
            <strong style="width:150px;"></strong>
            <span style="width:calc(100% - 150px)">{{EditReturn.backRemark || ''}}</span>
          </li>
        </ul>
        <el-button v-if="EditReturn.id" type="primary" size="small" class="eidtPlan" @click="dialogFormEditReturn = true"></el-button>
      </div>
    </div>

    <!--   -->
    <el-dialog class="giveUpAlert" title="" :visible.sync="dialogFormVisible"  width="45%">
      <el-form :model="giveUpAlert" :rules="rules" ref="giveUpAlert" label-width="100px" class="demo-ruleForm">
        <p></p>
        <el-form-item label="" prop="region">
          <el-select v-model="giveUpAlert.region" placeholder="" style="width:100%">
            <el-option
              v-for="item in backOut"  :key="item.id"
              :value="item.id" :label="item.label"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item style="margin-left:-100px">
          <el-button @click="dialogFormVisible = false "></el-button>
          <el-button type="success" @click="_giveUpEnter('giveUpAlert')"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <!--   -->
    <el-dialog class="giveUpAlert" title="" :visible.sync="dialogFormVisibleFriend"  width="45%">
      <el-form :model="giveUpAlert" :rules="rules" ref="addFriends" label-width="100px" class="demo-ruleForm">
        <el-form-item label="" prop="way" style="text-align:left;">
          <el-radio-group v-model="giveUpAlert.way" @change="addFriendTab">
            <el-radio-button v-for='(item,index) in ["","QQ"]' :label="item" :value="index" :key="index" ></el-radio-button>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" prop="Friendregion">
          <el-select v-model="giveUpAlert.Friendregion" placeholder="" style="width:100%">
            <el-option
            :value="item"
            :label="item"
            v-for="(item,index) in friendlist" :key="index">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item style="margin-left:-100px; "  >
          <el-button @click="dialogFormVisibleFriend = false"></el-button>
          <el-button type="success" @click="_FriendrEnter('addFriends')"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <!--   -->
    <el-dialog class="" title="" :visible.sync="dialogFormEditReturn"  width="45%">
      <el-form class="demo-ruleForm">
          <el-form-item label="">
            <el-select  placeholder="" v-model="EditReturn.backPlan" @change="showBackPlan($event)">
              <el-option v-for='(item,index) in editReturnList' :label="item.label" :value="item.id" :key="item.id"></el-option>
            </el-select>
            <el-date-picker
              v-model="EditReturn.backTime"
              type="datetime"
              value-format="yyyy-MM-dd HH:mm"
              format="yyyy-MM-dd HH:mm"
              v-if="showBackPlanId == 4 ? false : true"
              placeholder="">
            </el-date-picker>
          </el-form-item>

          <el-form-item label="" >
            <el-input type="textarea" style="width:74%;" v-model="EditReturn.backRemark"></el-input>
          </el-form-item>

          <el-form-item class="btn-right" >
            <el-button @click="dialogFormEditReturn = false;getStuFollowDetail() "></el-button>
            <el-button type="success" @click="EditReturnUp()"></el-button>
          </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import { idOptions, intentLubjectList, degreeList, sexList, levelList, classtimeList, cardNoList, editReturnList, backOut } from "./../data/data.js";
import { mapMutations } from 'vuex'
export default {
  props:['intentSubject'],
  data(){
    return {
      // 
      stuInfo: "",
      dialogFormVisible: false,
      dialogFormVisibleFriend: false,
      dialogFormEditReturn: false,
      stutentInfo: {},
      stuCardInfo: {},
      showBackPlanId:0,
      cityIdFk : [],
      friendlist: [],
      giveUpAlert:{
        region:"",
        Friendregion: "",
        way:""
      },
      EditReturn: {
        backPlan: "",
        backTime: "",
        backRemark: "",
        id: -1
      },
      rules: {
        region: [
            { required: true, message: '', trigger: 'change' }
          ],
        Friendregion: [
            { required: true, message: '', trigger: 'change' }
        ],
        way: [
            { required: true, message: '', trigger: 'change' }
        ],
        returnOff: [
            { required: true, message: '', trigger: 'change' }
        ]
      },
      //  
      idOptions,
      intentLubjectList,
      degreeList,
      sexList,
      levelList,
      classtimeList,
      cardNoList,
      editReturnList,
      backOut
    }
  },
  computed: {
    // 
    studentIntention(){
      const degree = this.stuInfo.degree;
      const value = [0, 5, 4, 2 ,0.5];
      const label = ["","","","",""];
      return {
        value : parseInt(degree) >= -1 ? value[degree] : 0 ,
        label : parseInt(degree) >= -1 ? label[degree] : ''
      }
    }
  },
  watch: {
    // 
   '$route': 'getrouterQuery'
   },
  created(){
    this.getrouterQuery();
  },
  methods:{
    getrouterQuery(){
      this.userId = this.$store.state.user.userInfo.userId;
      //ID
      this.stuId = this.$router.currentRoute.query.stuId;

      // 
      this.isAddFriends = this.$router.currentRoute.query.friend
      // 
      this.getStuFollowDetail();
      this.getStuBaseInfo();
      //console.log(this.stuCardList);
    },
    intentSubjectSum(){
      for(var i=0; i<this.intentSubject.length; i++){
        if(this.stuInfo.intentSubject == this.intentSubject[i].proId){
          return this.intentSubject[i].proName;
        }
      }
    },
    // 
    showBackPlan(val){
      this.showBackPlanId= val;
    },
    ...mapMutations([
      'STUDENT_INFO',
    ]),
    // 
    getStuBaseInfo(){
      this.API.getStuBaseInfo({
        stuId:this.stuId
      })
      .then(res=>{
        this.stuInfo = res.result[0].stuInfo;
        this.STUDENT_INFO(this.stuInfo);
        //this.studentIntention();
      }).catch(err => {
        console.log(err.message)
      });
    },
    // 
    addFriends(){
      // 
      this.giveUpAlert.way = "";
      this.giveUpAlert.Friendregion = "";
      // 
      if (this.isAddFriends == 0) {
        this.$message({
          message: '',
          type: 'warning'
        });
        return;
      }
      this.dialogFormVisibleFriend = true;
    },
    //QQ
    addFriendTab(){
      let type = -1;
      if(this.giveUpAlert.way == ""){ type = 2} else { type= 3 };
      this.API.qryStuContact({
        stuId: this.stuId
      })
      .then(res=>{
        //      
        if(this.giveUpAlert.way == ""){
          // this.friendlist = this.friendlist.concat(res.result[type].more,res.result[0].more)
          this.friendlist = [...res.result[type].more, ...res.result[0].more];
        }
        else{
          this.friendlist= res.result[type].more;
        }
        this.giveUpAlert.Friendregion = "";
      }).catch(err => {
        console.log(err.message)
      });
    },
    // 
    _FriendrEnter(formName){

      let type = -1;
      this.$refs[formName].validate((valid) => {
        if(!valid) return;
        // ID
        let userIdFk = this.userId; //this.$store.state.user.userData.userId;
        //  ID
        let stuIdFk = this.stuId;
        if(this.giveUpAlert.way == ""){ type = 2} else { type= 3 };
        let remark = this.giveUpAlert.Friendregion;

        this.API.addFriend({
          userIdFk,stuIdFk,type,remark
        })
        .then(res=>{
          this.$message({
            message: '',
            type: 'success'
          });
          this.dialogFormVisibleFriend = false;
        }).catch(err => {
          console.log(err.message)
        });
      })
    },
    // 
    edit(){
      this.$router.push({path:'edit-student',query:{stuId:this.stuId}});
    },
    //
    _giveUpEnter(formName){
      this.$refs[formName].validate((valid) => {
        if(!valid) return;

        this.API.delBelongTo({
          stuId: this.stuId
        })
        .then(res=>{
          this.$message({
            message: '',
            type: 'success'
          });
        }).catch(err => {
          console.log(err.message)
        });


        this.dialogFormVisible = false;
      })
    },
    // 
    getStuFollowDetail(){
      this.API.getStuFollowDetail({
        stuId: this.stuId,
        userId: this.userId
      })
      .then(res=>{
        this.EditReturn.backPlan = res.result.backPlan
        this.EditReturn.backTime = res.result.backTime
        this.EditReturn.backRemark = res.result.backRemark
        this.EditReturn.id = res.result.id
      }).catch(err => {
        for(var k in this.EditReturn){
          this.EditReturn[k] = "";
        }
        //console.log(err.message)
      });
    },
    // 
    EditReturnUp(){
      if (!String(this.EditReturn.backPlan)) {
        this.$message({
          message: '',
          type: 'warning'
        });
        return;
      }
      this.API.mdfStuFollowDetail(
        this.EditReturn
      )
      .then(res=>{

      }).catch(err => {
        console.log(err.message)
      });

      this.dialogFormEditReturn = false;
    },
  },
}
</script>

<style lang="scss" scoped>
  .studentInfo{
    margin: 20px 0px 0 20px;
    background: #fff;
    .studentHeader{
  		width: 100%;
  		margin-top: 10px;
      overflow: hidden;
      .btn-right{
        float: right;
        margin-right: 20px;
        margin-top: 7px;
        .btn{
          text-decoration: underline;

        }
      }
      .progress{
        float: left;
        margin: 0px 0px 0px 25px;
        span{
          float: left;
          font-size: 13px;
          color:#666;
          margin-top: 13px;
        }
        .xinxin{
          float: left;
          margin-top: 13px;
        }
      }
  		.slideTitle{
  			height: 40px;
  			line-height: 40px;
        float: left;
  			h3{
  				float: left;
  				margin-left: 10px;
  			}
  			span{
  				display: block;
  				float: left;
  				width: 4px;
  				height: 20px;
  				background: #38a6fa;
  				margin-top: 10px;
  			}
  		}
  	}
    .pay{
      overflow: hidden;
      .list{
        float: left;
        width: 80%;
      }
      .eidtPlan{
        float: left;
        margin-top: 40px;
      }

    }
    .list{
      padding: 10px 20px 20px 20px;
      overflow: hidden;
      border-bottom: 1px solid #f4f4f4;
      li{
        float: left;
        overflow: hidden;
        width: 33.333%;
        text-align: center;
        line-height: 40px;
        span{
          color: #2c2f33;
          display: block;
          float: left;
          width: 50%;
          text-align: left;
          &.study-use{
            line-height: 24px;
            margin-top: 7px;
            word-break: break-word;
          }
        }
        strong{
          color: #848e99;
          width: 50%;
          display: block;
          float: left;
          text-align: right;
        }
      }
    }
    .giveUpAlert{
      text-align: center;
      p{
        margin-bottom: 10px;
      }
    }
    .btn-right{
      text-align: center;
    }
  }
</style>



